@use '../../assets/shared' as *;

.header {
  background: $clr-primary;
  color: $clr-neutral-100;
  padding-block: 0.7rem;
}

.content {
  @include flex-row(1rem, center, space-between);
  width: min(94%, 1500px);
  margin-inline: auto;

  a {
    color: $clr-neutral-100;
    text-decoration: none;
  }

  h1 {
    font-weight: 400;
    font-size: 1.5rem;
    text-align: center
  }
}

.user {
  display: none;

  a img {
    width: 35px;
  }

  picture img {
    width: 50px;
  }
}

.white {
  filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(2%) hue-rotate(335deg) brightness(112%) contrast(101%);
}

@media (min-width: 600px) {
  .user {
    @include flex-row(0.8rem, center);
  }
}
